import React from 'react';
import { Link } from 'react-router-dom';
import { FiSearch } from 'react-icons/fi';

import './styles.css';

const Header = () => {
  return (
    <header id="header-blog">
      <nav>
        <Link to="/" className="logo">
          <FiSearch />
          <h1>Observador</h1>
        </Link>

        <form>
          <input type="text" placeholder="Procurando algo?" />
          <button type="submit">
            <FiSearch />
          </button>
        </form>
      </nav>
    </header>
  );
}

export default Header;
